// Import grid settings :
@import '../site_settings';
@import 'grid-settings';

// Generate custom states :
@include gridle_register_state( xs, (
    max-width : $xs,
    gutter-width : $gutter_xs
) );
@include gridle_register_state( sm , (
    min-width : $sm,
    gutter-width : $gutter_sm
) );
@include gridle_register_state( md, (
    min-width : $md,
    gutter-width : $gutter_md
) );
@include gridle_register_state( lg, (
    min-width : $lg,
    gutter-width : $gutter_lg
) );
@include gridle_register_state( xlg, (
    min-width : $xlg,
    gutter-width : $gutter_xlg
) );
@include gridle_register_state( xxlg, (
    min-width : $xxlg,
    gutter-width : $gutter_xxlg
) );


// Fix row and ontainer margins
.row {
    margin-left: -$gutter/2 !important;
    margin-right: -$gutter/2 !important;

    @include gridle_state(xs){
        margin-left: -$gutter_xs/2 !important;
        margin-right: -$gutter_xs/2 !important;
    }

    @include gridle_state(sm){
        margin-left: -$gutter_sm/2 !important;
        margin-right: -$gutter_sm/2 !important;
    }

    @include gridle_state(md){
        margin-left: -$gutter_md/2 !important;
        margin-right: -$gutter_md/2 !important;
    }

    @include gridle_state(lg){
        margin-left: -$gutter_lg/2 !important;
        margin-right: -$gutter_lg/2 !important;
    }

    @include gridle_state(xlg){
        margin-left: -$gutter_xlg/2 !important;
        margin-right: -$gutter_xlg/2 !important;
    }

    @include gridle_state(xxlg){
        margin-left: -$gutter_xxlg/2 !important;
        margin-right: -$gutter_xxlg/2 !important;
    }
}


.container {
    max-width: $max_container_width+$gutter;
    padding-left: $gutter/2 !important;
    padding-right: $gutter/2 !important;

    @include gridle_state(xs){
        max-width: $max_container_width+$gutter;
        padding-left: $gutter_xs/2 !important;
        padding-right: $gutter_xs/2 !important;
    }

    @include gridle_state(sm){
        max-width: $max_container_width+$gutter_sm;
        padding-left: $gutter_sm/2 !important;
        padding-right: $gutter_sm/2 !important;
    }

    @include gridle_state(md){
        max-width: $max_container_width+$gutter_md;
        padding-left: $gutter_md/2 !important;
        padding-right: $gutter_md/2 !important;
    }

    @include gridle_state(lg){
        max-width: $max_container_width+$gutter_lg;
        padding-left: $gutter_lg/2 !important;
        padding-right: $gutter_lg/2 !important;
    }

    @include gridle_state(xlg){
        max-width: $max_container_width+$gutter_xlg;
        padding-left: $gutter_xlg/2 !important;
        padding-right: $gutter_xlg/2 !important;
    }

    @include gridle_state(xxlg){
        max-width: $max_container_width+$gutter_xxlg;
        padding-left: $gutter_xxlg/2 !important;
        padding-right: $gutter_xxlg/2 !important;
    }
}


// Generate text align classes
@include gridle_generate_custom_class ( ('text-center','@','%state') ) {
    text-align: center;
}

@include gridle_generate_custom_class ( ('text-left','@','%state') ) {
    text-align: left;
}

@include gridle_generate_custom_class ( ('text-right','@','%state') ) {
    text-align: right;
}


// Generate float classes
@include gridle_generate_custom_class ( ('float-left','@','%state') ) {
    float: left;
}

@include gridle_generate_custom_class ( ('float-right','@','%state') ) {
    float: right;
}




// Generate classes :
@include gridle_generate_classes();

